/*!
 * WordPress Posts
 *
 * @author [GrottoPress](https://www.grottopress.com)
 * @author [N Atta Kusi Adusei](https://twitter.com/akadusei)
 */
/**
 * Base
 */
.posts-wrap {
  margin-top: 20px;
  margin-bottom: 20px;
  font-size: 14px;
  line-height: 160%;
}
.posts-wrap:first-child {
  margin-top: 0;
}
.posts-wrap:last-child {
  margin-bottom: 0;
}

.post-wrap {
  margin-top: 10px;
  margin-bottom: 10px;
}
.post-wrap:first-child {
  margin-top: 0;
}
.post-wrap:last-child {
  margin-bottom: 0;
}
.post-wrap::after {
  content: "";
  display: table;
  clear: both;
}
.post-wrap .entry-title,
.post-wrap .entry-summary,
.post-wrap .entry-meta {
  margin-top: 5px;
  margin-bottom: 5px;
}
.post-wrap .entry-title:first-child,
.post-wrap .entry-summary:first-child,
.post-wrap .entry-meta:first-child {
  margin-top: 0;
}
.post-wrap .entry-title:last-child,
.post-wrap .entry-summary:last-child,
.post-wrap .entry-meta:last-child {
  margin-bottom: 0;
}
.post-wrap .entry-title {
  font-size: 1.14em;
}
.post-wrap .entry-meta {
  display: block;
  color: #999;
  font-size: 0.93em;
}
.post-wrap > header,
.post-wrap > footer {
  margin-top: 10px;
  margin-bottom: 10px;
}
.post-wrap > header:first-child,
.post-wrap > footer:first-child {
  margin-top: 0;
}
.post-wrap > header:last-child,
.post-wrap > footer:last-child {
  margin-bottom: 0;
}
.post-wrap > header {
  margin-top: 0;
}
.post-wrap .more-link {
  font-size: 0.93em;
}

.big > .post-wrap {
  margin-top: 20px;
  margin-bottom: 20px;
}
.big > .post-wrap:first-child {
  margin-top: 0;
}
.big > .post-wrap:last-child {
  margin-bottom: 0;
}
.big > .post-wrap > header > .entry-title,
.big > .post-wrap > .entry-summary,
.big > .post-wrap > footer > .entry-meta,
.big > .post-wrap > header > .entry-meta {
  margin-top: 10px;
  margin-bottom: 10px;
}
.big > .post-wrap > header > .entry-title:first-child,
.big > .post-wrap > .entry-summary:first-child,
.big > .post-wrap > footer > .entry-meta:first-child,
.big > .post-wrap > header > .entry-meta:first-child {
  margin-top: 0;
}
.big > .post-wrap > header > .entry-title:last-child,
.big > .post-wrap > .entry-summary:last-child,
.big > .post-wrap > footer > .entry-meta:last-child,
.big > .post-wrap > header > .entry-meta:last-child {
  margin-bottom: 0;
}
.big > .post-wrap > header > .entry-title {
  font-size: 1.43em;
}

@media (min-width: 900px) {
  .big > .post-wrap > header > .entry-title {
    font-size: 1.71em;
  }
}
/**
 * Content
 */
.post-wrap .entry-content {
  line-height: 180%;
  font-size: 1.14em;
  margin-top: 20px;
  margin-bottom: 20px;
}
.post-wrap .entry-content:first-child {
  margin-top: 0;
}
.post-wrap .entry-content:last-child {
  margin-bottom: 0;
}
.post-wrap .entry-content::after {
  content: "";
  display: table;
  clear: both;
}

.show-content > .post-wrap .more-link {
  font-size: 0.93em;
}
.show-content > .post-wrap > header,
.show-content > .post-wrap > footer,
.show-content > .post-wrap > header > .entry-title,
.show-content > .post-wrap > .entry-summary,
.show-content > .post-wrap > header > .entry-meta,
.show-content > .post-wrap > footer > .entry-meta {
  margin-top: 20px;
  margin-bottom: 20px;
}
.show-content > .post-wrap > header:first-child,
.show-content > .post-wrap > footer:first-child,
.show-content > .post-wrap > header > .entry-title:first-child,
.show-content > .post-wrap > .entry-summary:first-child,
.show-content > .post-wrap > header > .entry-meta:first-child,
.show-content > .post-wrap > footer > .entry-meta:first-child {
  margin-top: 0;
}
.show-content > .post-wrap > header:last-child,
.show-content > .post-wrap > footer:last-child,
.show-content > .post-wrap > header > .entry-title:last-child,
.show-content > .post-wrap > .entry-summary:last-child,
.show-content > .post-wrap > header > .entry-meta:last-child,
.show-content > .post-wrap > footer > .entry-meta:last-child {
  margin-bottom: 0;
}

/**
 * Lists
 */
.posts-wrap li .post-wrap,
.posts-wrap li .post-wrap .entry-title,
.posts-wrap li .post-wrap .entry-summary,
.posts-wrap li .post-wrap .entry-meta {
  margin-top: 5px;
  margin-bottom: 5px;
}
.posts-wrap li .post-wrap:first-child,
.posts-wrap li .post-wrap .entry-title:first-child,
.posts-wrap li .post-wrap .entry-summary:first-child,
.posts-wrap li .post-wrap .entry-meta:first-child {
  margin-top: 0;
}
.posts-wrap li .post-wrap:last-child,
.posts-wrap li .post-wrap .entry-title:last-child,
.posts-wrap li .post-wrap .entry-summary:last-child,
.posts-wrap li .post-wrap .entry-meta:last-child {
  margin-bottom: 0;
}
.posts-wrap li .post-wrap .entry-title {
  font-weight: normal;
  font-size: 1em;
}

/**
 * Archive
 */
.archive-posts > .post-wrap,
.archive-posts > li > .post-wrap {
  border-bottom: 1px solid #ddd;
}
.archive-posts > li > .post-wrap {
  padding-bottom: 5px;
  margin-top: 5px;
  margin-bottom: 5px;
}
.archive-posts > li > .post-wrap:first-child {
  margin-top: 0;
}
.archive-posts > li > .post-wrap:last-child {
  margin-bottom: 0;
}
.archive-posts > .post-wrap {
  padding-bottom: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
}
.archive-posts > .post-wrap:first-child {
  margin-top: 0;
}
.archive-posts > .post-wrap:last-child {
  margin-bottom: 0;
}
.archive-posts.big > .post-wrap {
  padding-bottom: 16px;
  margin-top: 16px;
  margin-bottom: 16px;
}
.archive-posts.big > .post-wrap:first-child {
  margin-top: 0;
}
.archive-posts.big > .post-wrap:last-child {
  margin-bottom: 0;
}
.archive-posts.show-content > .post-wrap {
  padding-bottom: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
}
.archive-posts.show-content > .post-wrap:first-child {
  margin-top: 0;
}
.archive-posts.show-content > .post-wrap:last-child {
  margin-bottom: 0;
}
.archive-posts > .post-wrap:last-child,
.archive-posts > li > .post-wrap:last-child {
  padding-bottom: 0;
  border-bottom: 0;
}

/**
 * Grid Layout
 */
.layout-grid.posts-wrap > .post-wrap {
  margin-top: 16px;
  margin-bottom: 16px;
}
.layout-grid.posts-wrap > .post-wrap:first-child {
  margin-top: 0;
}
.layout-grid.posts-wrap > .post-wrap:last-child {
  margin-bottom: 0;
}
.layout-grid.posts-wrap > .post-wrap.big > header > .entry-title {
  font-size: 1.43em;
}
.layout-grid.posts-wrap .pagination {
  margin-top: 16px;
  margin-bottom: 16px;
  text-align: center;
}
.layout-grid.posts-wrap .pagination:first-child {
  margin-top: 0;
}
.layout-grid.posts-wrap .pagination:last-child {
  margin-bottom: 0;
}

@media (min-width: 600px) {
  .layout-grid.posts-wrap {
    box-sizing: border-box;
    display: flex;
    flex-wrap: wrap;
    margin: -8px;
  }
  .layout-grid.posts-wrap *,
  .layout-grid.posts-wrap *::before,
  .layout-grid.posts-wrap *::after, .layout-grid.posts-wrap::before, .layout-grid.posts-wrap::after {
    box-sizing: inherit;
  }
  .layout-grid.posts-wrap:first-child {
    margin-top: -8px;
  }
  .layout-grid.posts-wrap:last-child {
    margin-bottom: -8px;
  }
  .layout-grid.posts-wrap > * {
    flex: none;
    padding: 8px;
    width: 100%;
  }
  .layout-grid.posts-wrap > .post-wrap {
    margin: 0;
    width: 50%;
  }
  .layout-grid.posts-wrap .pagination {
    margin: 0;
  }
}
@media (min-width: 900px) {
  .layout-grid.posts-wrap > .post-wrap {
    width: 33.3333%;
  }
}
/**
 * Thumbnails
 */
.post-wrap .thumb,
.post-wrap .avatar {
  float: left;
  margin-right: 10px;
}
.post-wrap .entry-meta .avatar {
  float: none;
  margin-right: 0;
  display: inline-block;
  zoom: 1;
  *display: inline;
  vertical-align: top;
}

/*!rtl:begin:ignore*/
.img-align-left > .post-wrap > .thumb,
.img-align-left > .post-wrap > a > .thumb,
.img-align-left > .post-wrap > .avatar {
  float: left;
}

.img-align-right > .post-wrap > .thumb,
.img-align-right > .post-wrap > a > .thumb,
.img-align-right > .post-wrap > .avatar {
  float: right;
  margin-right: 0;
  margin-left: 10px;
}

/*!rtl:end:ignore*/
.img-align-none > .post-wrap > .thumb,
.img-align-none > .post-wrap > a > .thumb,
.img-align-none > .post-wrap > .avatar {
  float: none;
  margin-right: 0;
}

/**
 * Pagination
 */
.posts-wrap .pagination {
  color: #999;
  font-size: 1em;
  font-weight: bold;
  margin-top: 10px;
  margin-bottom: 10px;
}
.posts-wrap .pagination:first-child {
  margin-top: 0;
}
.posts-wrap .pagination:last-child {
  margin-bottom: 0;
}
.posts-wrap .pagination .page-numbers,
.posts-wrap .pagination a {
  padding: 5px;
}
.posts-wrap .pagination .page-numbers.dots {
  padding: 0;
}
.posts-wrap .pagination .page-numbers.current {
  text-decoration: underline;
}
.posts-wrap.big .pagination {
  margin-top: 20px;
  margin-bottom: 20px;
}
.posts-wrap.big .pagination:first-child {
  margin-top: 0;
}
.posts-wrap.big .pagination:last-child {
  margin-bottom: 0;
}