/**
 * Archive
 */

.archive-posts {
    > .post-wrap,
    > li > .post-wrap {
        border-bottom: 1px solid #ddd;
    }

    > li > .post-wrap {
        padding-bottom: map-get($gwpp-gutter, tiny);
        @include paragraph(map-get($gwpp-gutter, tiny));
    }

    > .post-wrap {
        padding-bottom: map-get($gwpp-gutter, small);
        @include paragraph(map-get($gwpp-gutter, small));
    }

    &.big > .post-wrap {
        padding-bottom: map-get($gwpp-gutter, normal) * 0.8;
        @include paragraph(map-get($gwpp-gutter, normal) * 0.8);
    }

    &.show-content > .post-wrap {
        padding-bottom: map-get($gwpp-gutter, normal);
        @include paragraph(map-get($gwpp-gutter, normal));
    }

    > .post-wrap:last-child,
    > li > .post-wrap:last-child {
        padding-bottom: 0;
        border-bottom: 0;
    }
}
